import axios from '@/libs/axios'

export default class DebtRegistrationApis {
    // eslint-disable-next-line no-return-await
    formulaCharge = async data => await axios.get('manager/debt/formula', {
        params: data,
        headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
    })

    // eslint-disable-next-line no-return-await
    debtDetailList = async data => await axios.get('manager/debt/detail', {
        params: data,
        headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
    })

    // eslint-disable-next-line no-return-await
    debtStore = async data => await axios.post('manager/debt/store-etc', data, {
        headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
    })

    // eslint-disable-next-line no-return-await
    debtUpdate = async data => await axios.put('manager/debt/update', data, {
        headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
    })

    // eslint-disable-next-line no-return-await
    debtDelete = async data => await axios.delete('manager/debt/delete', { data })

    // eslint-disable-next-line no-return-await
    debtRefresh = async data => await axios.post('manager/debt/refresh', data)
}
